.header{
  padding: 25px 0;
}
.header-block{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-item{
  list-style-type: none;
  font-family: 'PT Sans';
font-weight: 700;
font-size: 20px;
color: #000000;
@include small-tablet{
  margin-bottom: 30px;
  font-size: 30px;
}
}
.header-list{
  display: flex;
  :not(:last-child){
    margin-right: 45px;
  }
  @include small-tablet{
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    width: 80%;
    padding: 30px;
    height: 100vh;
    background-color: #fff;
    margin: 0;
    transform: translateX(-150%);
    transition: transform 0.3s ease-in-out;
  }
}
.header-item a{
  color: black;
}
.header-list-active{
  transform: none;
}
