.contact{
  background-color: #434242;
  padding: 0;
  padding-top: 100px;
  padding-bottom: 50px;
}
.container-contact{
  display: flex;
  justify-content: space-around;
  @include tablet{
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
.contact-text{
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 32px;
  color: #FFFFFF;
}
.contact-text-span{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 32px;
  color: #FFFFFF;
}
.contact-text-block{
  display: flex;
  flex-direction: column;
  :not(:last-child){
    margin-bottom: 30px;
  }
  max-width: 370px;
}
.contact-text-icon{
  max-width: 185px;
  display: flex;
  justify-content: space-between;
  @include tablet{
    text-align: center;
    max-width: 370px;
    justify-content: space-around;
  }
}
.contact-map{
  @include tablet{
    margin-bottom: 50px;
  }

}
iframe{
  @include mini-phone{
    width: 300px;
    height: 300px;
  }
}
