.stock{
  padding-top: 70px;
}
.stock-title{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 48px;
  color: #000000;
  text-align: center;
  margin: 0 50px;
}
.stock-block-title{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 70px;
}
.stock-line{
  width: 250px;
  height: 2px;
  margin: 0;
  border: none;
  border-top: 3px dashed #000000;
}
.stock-block-card{
  display: flex;
  justify-content: space-between;
  @include big-desktop{
    flex-direction: column;
    align-items: center;
  }
}
.stock-card{
  background-color: #434242;
  padding: 40px 50px;
  text-align: center;
  max-width: 420px;
  max-height: 559px;
  border-radius: 10px;
  @include big-desktop{
      margin-bottom: 50px;
  }
}
.stock-card-img{
  border-radius: 50%;
  border: 1px solid white;
}
.card-stock{
  display: flex;
  justify-content: space-between;

}
.stock-card-title{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 22px;
  color: #FFFFFF;
  margin: 20px 0;
}
.stock-card-descr{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 20px;
  color: #FFFFFF;
  margin: 0;
  margin-bottom: 50px;
  height: 93px;
  @include mini-phone{
    font-size: 17px;
  }
}
.stock-card-weight{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 20px;
  color: #FFFFFF;
  margin: 0;
  margin-bottom: 20px;
}
.stock-card-price{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 20px;
  color: #FFFFFF;
  margin: 0;
  @include mini{
    font-size: 16px;
  }
}
.price-old::before{
  content: "";
  border-bottom: 3px solid #fb0808;
  position: absolute;
  left: -5px;
  width:65%;
  height: 50%;
  transform: rotate(-50deg);
}
.price-old{
  position: relative;
}
