.menu-title{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 48px;
  color: #000000;
  text-align: center;
  margin: 0;
  padding-bottom: 70px;
  @include phone{
    font-size: 34px;
  }
}
.menu-block-title{
  background-image: url("../../img/backmenu-card.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  text-align: center;
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 32px;
  color: #FFFFFF;
  padding: 40px 5px;
  margin: 0;
}
.menu-block-title-text{
  padding-top: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  @include big-desktop{
    padding-top: 50px;
}
}
.menu{
  padding-top: 200px;
  padding-bottom: 50px;
  background-image: url("../../img/backmenu.jpg");
  background-color: #434242;
  background-size: cover;
  background-repeat: no-repeat;
  @include small-tablet{
    padding-top: 70px;
  }
}
.menu-container{
  display: grid;
  grid-template-columns: repeat(3, 25%);
  justify-content: space-between;
  @include big-desktop{
    grid-template-columns: repeat(2, 50%);
  justify-content: space-around;
}
@include small-tablet{
  grid-template-columns: repeat(1, 50%);
}
@include mobile{
  grid-template-columns: repeat(1, 80%);
}
@include mini-phone{
  grid-template-columns: repeat(1, 100%);
}
}
.menu-block-item{
  list-style-type: none;
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.75);
  @include big-desktop{
    font-size: 20px;
  }
}
.menu-block-price{
  display: flex;
  justify-content: space-around;
}
.menu-block-list{
  margin: 0;
  margin-top: 40px;
  padding: 0;
  :not(:last-child){
    margin-bottom: 10px;
  }
}
.menu-block{
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
}
