.info{
  padding-top: 60px;
  padding-bottom: 60px;
  background-image: url("../../img/backinfo.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  backdrop-filter: blur(2px);
}
.info-holl{
  border-radius: 50%;
  margin-right: 200px;
  margin-left: 100px;
  @include big-desktop{
    margin: 0;
  }
  @include phone{
    width: 270px;
    height: 270px;
  }
}
.info-title{
  font-family: 'PT Sans';
  font-weight: 700;
  font-size: 36px;
  color: #FFFFFF;
}
.info-descr{
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 20px;
  color: #FFFFFF;
  @include phone{
    font-size: 16px;
  }
}
.container-info{
  display: flex;
  align-items: center;
  @include big-desktop{
    flex-direction: column;
  }
}
.info-text{
  @include big-desktop{
    max-width: 1000px;
  }
  @include tablet{
    max-width: 700px;
  }
}
