.deliver{
  background-image: url("../../img/backdeliver.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 110px 0;
}
.deliver-block{
  background-color: rgba(67, 67, 67, 0.75);
  backdrop-filter: blur(4.5px);
padding: 30px;
max-width: 650px;
margin: 0 auto;
}
.deliver-item{
  list-style-type: none;
  padding: 0;
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 32px;
  color: #FFFFFF;
  @include small-phone{
    font-size: 20px;
  }
  @include phone{
    font-size: 16px;
  }
}
.deliver-list{
  padding: 0;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 40px;
}
.deliver-descr{
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.95);
  margin: 0;
  @include small-tablet{
    font-size: 26px;
  }
  @include mobile{
    font-size: 24px;
    text-align: center;
  }
  @include phone{
    font-size: 18px;
    text-align: center;
  }
}
.deliver-button{
  padding: 10px 55px;
  border-radius: 8px;
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 24px;
  color: #000000;
  background-color: white;
}
.diliver-button-block{
  text-align: center;
}
