/* stylelint-disable */

@mixin small-phone {
  @media (max-width: (660px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin phone {
  @media (max-width: (430px)) {
    @content;
  }
}

@mixin mini-phone {
  @media (max-width: (375px)) {
    @content;
  }
}

@mixin mini {
  @media (max-width: (325px)) {
    @content;
  }
}
